import { Container, Grid, Box } from '@mui/material';
import { Link } from 'react-router-dom';
import { styled, css } from '@mui/material/styles';
import { useLocation } from 'react-router-dom'

const MenuItem = styled(Link)(
  ({ theme }) => css`
    font-weight: 500;
    font-size: 14px;
    line-height: 21px;
    letter-spacing: 0.1px;
    color: rgba(0, 0, 0, 0.87);
    display: flex;
    align-items: center;
    margin-bottom: 10px;
    padding: 10px 10px;
    max-width: 239px;
    height: 40px;
    width: 100%;
    &:hover {
      color: ${theme.palette.action.hover};
      background: ${theme.palette.action.active};
    }
  `
);

const style = {
  active: {
    background: '#E3F2FD',
    color: '#2196F3'
  }
}

const BuyerCMSLayout = ({ children }) => {
  const {pathname} = useLocation();
  const menuList = [
    {
      groupId: 1,
      groupTitle: '',
      groupUrl: '',
      groupList: [
        { id: 1, title: 'Dashboard', image: 'dashboard.png', url: '/buyer/dashboard' },
        { id: 2, title: 'User Info', image: 'user.png', url: '/buyer/user-info' },
        { id: 3, title: 'Messages', image: 'message.png', url: '/buyer/message' },
        { id: 4, title: 'Purchases', image: 'purchase.png', url: '/buyer/purchase' }
      ]
    },
    {
      groupId: 2,
      groupTitle: 'Settings',
      groupUrl: '',
      groupList: [
        { id: 1, title: 'Main Settings', image: 'setting.png', url: '/buyer/setting' },
        { id: 2, title: 'Notifications', image: 'noti.png', url: '/buyer/notification' },
        { id: 3, title: 'Payment', image: 'payment.png', url: '/buyer/payment-method' }
      ]
    }
  ];
  return (
    <Box sx={{ background: '#E5E5E5' }}>
      <Container maxWidth="lg" style={{ padding: '48px 0' }}>
        <Grid container spacing={0}>
          <Grid item xs={12} md={3}>
            <Box display="flex" alignItems="center">
              <img src={require(`src/assets/images/user-default.png`)} alt="user-default.png" style={{ width: '70px', height: '70px', borderRadius: '50%' }} />
              <Box sx={{ fontWeight: '500', fontSize: '18px', lineHeight: '21px', letterSpacing: '0.1px', marginLeft: '17px' }}>InApps</Box>
            </Box>
            <Box>
              {menuList && menuList.length > 0
                ? menuList.map((group) => {
                    return (
                      <Box key={group.groupId}>
                        <Box sx={{ fontSize: '14px', lineHeight: '21px', letterSpacing: '0.25px', color: '#9E9E9E', margin: '25px auto' }}>{group.groupTitle}</Box>
                        <Box>
                          {group.groupList && group.groupList.length > 0
                            ? group.groupList.map((item) => {
                                return (
                                  <MenuItem style={pathname === item.url ? style.active : {}} key={item.id} to={item.url}>
                                    <img src={require(`src/assets/images/icons/${item.image}`)} alt={item.title} style={{ width: '18px', height: '18px', marginRight: '35px' }} />
                                    {item.title}
                                  </MenuItem>
                                );
                              })
                            : ''}
                        </Box>
                      </Box>
                    );
                  })
                : ''}
            </Box>
          </Grid>
          <Grid item xs={12} md={9}>
            {children}
          </Grid>
        </Grid>
      </Container>
    </Box>
  );
};

export default BuyerCMSLayout;
